.intro__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.intro__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  margin: 0;
}

.intro__slogan {
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  margin: 0;
  text-transform: uppercase;
}

.intro__button {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  width: 280px;
  height: 40px;
}
