.example__stats {
  display: grid;
  grid-template-columns: 124px 124px;

  @media (min-width: @tablet-width) {
    display: flex;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 124px 124px;
    align-items: center;
    justify-items: center;
  }
}

.example__wrapper {
  @media (min-width: @desktop-width) {
    display: flex;
  }
}

.example__stats-item:last-child {
  grid-column: -1 / 1;
}

.example__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  margin: 0;
}

.example__stats-title {
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
}

.example__stats-text {
  font-family: @font-lato;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: @dark-grey;
}

.example__stats-expenses {
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
}

.slider {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: relative;
  width: 280px;
  height: 256px;

  @media (min-width: @tablet-width) {
    width: 560px;
    height: 512px;
  }
}

.slide-before,
.slide-after {
  position: absolute;
  width: inherit;
  height: inherit;
}
